@import url(https://fonts.googleapis.com/css?family=Roboto:500);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.event{
  position: relative;
  background-color: #333333;
}
.event-location{
  font-size: .8rem;
}
.location-icon{
  top: 2.5px;
}

.details{
  font-size: .8rem;
}


.toggle-details{
  position: absolute;
  right: 0;
  bottom: 0;
  background-color: transparent;
  border: none;
  color: #e2d36b;
  font-size: .9rem;
}

.EventList{
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.Alert{
  font-size: .8rem;
}
.info{
  position: absolute;
  bottom: 35px;
  left: 125px;
}
.error{
  position: absolute;
  top: 50px;
}

.CitySearch{
  position: relative;
}
.city-search{
  outline: none;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #333333;
  width: 100%;
  transition: all 500ms;
  color: white;
}
.city-search:focus{
  border-bottom: 1px solid #e2d36b;
}
.suggestions{
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 55px;
  background-color:#444444;
  z-index: 10000;
  border-radius: 0 0 5px 5px;
}
.suggestions li{
  transition: all 300ms;
  padding-left: .5rem;
}
.suggestions li:hover{
  cursor: pointer;
  -webkit-transform: scale(1.005);
          transform: scale(1.005);
}
.numOfEvents{
  width: 40px;
  height: 25px;
  background-color: #333333;
  border: none;
  text-align: center;
  color: white;
  transition: all 500ms;
  font-size: 12px;
}
.numOfEvents:focus{
  outline: none;
  border: 1px solid #e2d36b;
}

.numOfEvents-header{
  font-size: .8rem;
  position: relative;
}


.navbar{
  background-color: #444444;
  box-shadow: 5px 0 5px rgba(0,0,0,0.2);
  height: 10vh;
}
.App{
  color: white;
  position: relative;
}

.container{
  position: relative;
}
.body{
  height: 90vh;
}

.charts-section{
  background-color: #333333;
}

.events-section{
  background-color: #222222;
}
/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #e2d36b;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #e2d36b, 0 0 5px #e2d36b;
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #e2d36b;
  border-left-color: #e2d36b;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
